.card{
  width: 100;
  height: 100;
  padding: 8px;
  background-color: whitesmoke;
  border-radius: 7px;
  margin-right: 1em;
  margin-left: 1em;

  display: flex;
  align-items: center;
  justify-content: center;

  transition: box-shadow 0.25s ease-in-out;
}

.card:hover{
  box-shadow: 0px 0px 8px #00E0FF; 
}

/* RESPONSIVE ======*/

@media (max-width:700px){

  .card{
    width: 60px;
    height: 60px;
    margin-top: 12px;
  }
}