.text{
  margin-bottom: 28px;  
  animation: slideContent 1.1s;
}

@keyframes slideContent{
  from{
    transform: translateX(-60px);
  }
  to{
    transform: translateX(0px);
  }
}

.text p{
  width: 100%;
  font-size: 1.2em;
  font-weight: 250;
  line-height: 1.3;
}

/* RESPONSIVE =====*/
@media(max-width: 1200px){
  .text{
    text-align: center;
  }
  .text p{
    width: 100%;
  }
}

@media(max-width: 700px){
  .text p{
    font-size: 0.9em;
  }
}

