header{ 
  background-color: #222;
  padding: 6px;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

header h1{
  font-size: 2.6em;
  font-weight: 200;
  margin-left: 1em;
  cursor: pointer;
}

header nav{
  display: flex;
  align-items: center;

}

header nav a {

  margin-right: 2em;
  margin-left: 27em;
  font-size: 1.6em;
}

header h1,a {

  color: #00E0FF;
}

header a:hover, h1:hover{

  color: #FFF;
  transition: 0.15s ease-in ;
}

/* RESPONSIVE ==================== */

@media(max-width:700px){

  header{
    flex-direction: column;
  }

  header nav{
    margin-top: 16px;
  }

  header h1 {

    font-size: 2.2em;
  }

  header nav a {

    font-size: 1.2em;
  }
}